<template>
  <div>
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.qnaDatetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">{{ item.qnaContent }}</td>
      </tr>
    </table>
    <br />
    <div class="text-center">
      <h3>답변</h3>
    </div>
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>작성자</th>
        <td>{{ item.replyUserid }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.replyDatetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">{{ item.replyContent }}</td>
      </tr>
    </table>

    <div class="text-center">
      <router-link :to="'/list'">
        <button class="btn btn-primary">목록</button>
      </router-link>
      <router-link :to="'/update?qnaNo=' + item.qnaNo">
        <button class="btn btn-primary">수정</button>
      </router-link>
      <router-link :to="'/delete?qnaNo=' + item.qnaNo">
        <button class="btn btn-primary">삭제</button>
      </router-link>
    </div>
  </div>
</template>


<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "read",
  data: function() {
    return {
      item: {}
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    axios
      .get(`http://localhost:9999/happyhouse/api/qna/${params.get("qnaNo")}`)
      .then(({ data }) => {
        this.item = data;
      });
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    }
  }
};
</script>

<style></style>